:root {
  --accent-color: #3455DB;
  --text-color: #173035;
  --shadow: 0 5px 40px rgba(99, 133, 134, 0.25);
  --border-color: #E0EBEB;
  --white: #ffffff; }

body {
  font-family: "Baloo Da 2", cursive;
  font-size: 16px;
  color: var(--text-color); }

h1, .h1 {
  color: var(--text-color); }

h1, .h1 {
  font-size: 2.5rem;
  line-height: 1.5; }

input[type="number"] {
  box-shadow: none;
  height: 56px;
  padding: 0 0.5rem 0 1rem;
  border-radius: 5px;
  border: 2px solid var(--border-color); }
  input[type="number"]:focus {
    border-color: var(--accent-color);
    outline: none; }

select {
  height: 56px;
  padding: 0 3rem 0 1rem;
  font-size: 1rem;
  border-radius: 5px;
  border: 2px solid var(--border-color); }
  select:focus {
    border-color: var(--accent-color);
    outline: none; }

button, .btn {
  padding: 0 2rem;
  height: 56px;
  line-height: 56px;
  background: var(--text-color);
  border: 1px solid var(--text-color);
  color: var(--white);
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.2s; }
  button:hover, button:focus, .btn:hover, .btn:focus {
    background: var(--accent-color);
    border: 1px solid var(--accent-color);
    outline: none; }

a {
  color: var(--accent-color);
  transition: all 0.2s; }
  a:hover {
    text-decoration: none; }

.container {
  max-width: 1200px;
  width: calc(100vw - 64px);
  padding-left: 32px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
  min-height: calc(100vh + 1px); }

.site-header {
  padding: 32px 0;
  margin-bottom: 32px; }

.logo {
  font-size: 1.75rem;
  font-weight: 700;
  color: var(--accent-color);
  text-decoration: none; }

.site-hero {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 6rem; }

.hero-left {
  flex: 0 0 50%; }

.hero-title {
  margin-bottom: 4rem; }

.hero-right {
  flex: 0 0 40%; }
  .hero-right .hero-image {
    max-width: 100%; }

.generator-form-wrap {
  display: inline-flex;
  padding: 2rem;
  box-shadow: var(--shadow);
  border-radius: 15px;
  font-size: 1.25rem;
  font-weight: 500; }

.generator-form {
  display: flex;
  align-items: center; }

.input-number {
  width: 64px;
  font-size: 1.25rem;
  font-weight: 500; }

.relative {
  position: relative;
  display: inline-flex; }

.input-select {
  font-size: 1.25rem;
  font-weight: 500;
  appearance: none; }

.select-icon {
  position: absolute;
  right: 0;
  top: 0;
  width: 40px;
  height: 56px;
  text-align: center;
  pointer-events: none; }
  .select-icon svg {
    width: 20px;
    height: 56px;
    vertical-align: middle; }

.submit-btn {
  position: relative; }
  .submit-btn.loading:before {
    content: "";
    width: 100%;
    height: 100%;
    background: var(--text-color);
    position: absolute;
    left: 0;
    top: 0; }
  .submit-btn.loading:after {
    content: "";
    width: 30px;
    height: 30px;
    border: 2px solid var(--white);
    border-left-color: transparent;
    border-radius: 50%;
    position: absolute;
    left: 50%;
    top: 50%;
    box-sizing: border-box;
    margin-left: -15px;
    margin-top: -15px;
    animation: spin 0.4s linear infinite; }

@keyframes spin {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

@media screen and (min-width: 768px) {
  .generator-form > * {
    margin-right: 1rem; }
  .generator-form > :last-child {
    margin-right: 0; } }

.result {
  width: 100%;
  max-width: 800px;
  margin: 4rem auto 6rem;
  font-size: 1.25rem;
  font-weight: 500;
  line-height: 1.8;
  flex-grow: 1; }
  .result p {
    margin-bottom: 2.5rem; }

.site-footer {
  padding: 3rem 0;
  border-top: 1px solid var(--border-color);
  text-align: center; }
